<template>
    <!-- title="我要咨询"  -->
    <el-dialog class="custom" width="720px" :modal="false" :visible.sync="isOpen"
        :before-close="closed" :close-on-click-modal="false" v-dialogDrag >
        
        <div class="product_consult">
            <div class="hd">
                <img src="../../assets/byrj_logo.png" alt="logo">
                我要咨询
            </div>
            <div class="form">
                <dl>
                    <dd><label>用户名</label><input type="text" ref="account" v-model="formData.account" placeholder="请输入用户名"></dd>
                    <dd><label>联系人</label><input type="text" ref="contacts" v-model="formData.contacts" placeholder="请输入联系人"></dd>
                    <dd><label>电话</label><input type="text" ref="phone" v-model="formData.phone" placeholder="请输入电话"></dd>
                    <dd><label>邮箱</label><input type="text" ref="email" v-model="formData.email" placeholder="请输入邮箱"></dd>
                </dl>
                <div class="textarea">
                    <textarea v-model="formData.content" ref="content" placeholder="请输入咨询内容 (限200字)"></textarea>
                    <span>限200字</span>
                </div>
            </div>
            <div class="bm">
                <div class="ll">免责声明：由于我们人手有限，不能对所有咨询一一答覆，敬请谅解。</div>
                <div class="rr text-right"><a href="javascript:;" @click="submit">提交</a></div>
                <div class="clr"></div>
            </div>
        </div>

        <DialogAlert :open="$dialogAlert.isOpen" :message="$dialogAlert.message" @close="closeAlert" />

    </el-dialog>
</template>

<script>
import Cookie from 'vue-cookie'
import DialogAlert from '@src/components/dialog.alert/dialog.alert'

export default {
    components: { DialogAlert },
    props: {
        'title': { default: '未命名' },
        'open': { default: false },
    },
    watch: {
        'open' (v) {
            this.isOpen = v
            if (v) {
                const UserInfo = Cookie.get('UserInfo')
                if (UserInfo) {
                    const UserInfo_JSON = JSON.parse(UserInfo)
                    this.formData['account'] = UserInfo_JSON.account
                    this.formData['contacts'] = UserInfo_JSON.contacts
                    this.formData['phone'] = UserInfo_JSON.phone
                }
            }
        },
    },
    methods: {

        closeAlert () {
            this.$refs[this.$dialogAlert.ref].focus()
            this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: false, message: '', ref: '' })
            this.$forceUpdate()
        },

        closed () {
            this.isOpen = false
            this.$emit('close', true)
        },
        submit () {

            if (!this.formData.account) {
				this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
                    message: '请填写用户名', ref: 'account' })
                this.$forceUpdate();
                return false;
            }
            if (!this.formData.contacts) {
				this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
                    message: '请填写联系人', ref: 'contacts' })
                this.$forceUpdate();
                return false;
            }
            if (!this.formData.phone) {
				this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
                    message: '请填写电话', ref: 'phone' })
                this.$forceUpdate();
                return false;
            }
            // if (!this.formData.email) {
			// 	this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
            //         message: '请填写邮箱', ref: 'email' })
            //     this.$forceUpdate();
            //     return false;
            // }
            if (!this.formData.content) {
				this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
                    message: '请填写内容', ref: 'content' })
                this.$forceUpdate();
                return false;
            } else if (this.formData.content.length > 200){
                this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
                    message: '内容不超200字', ref: 'content' })
                this.$forceUpdate();
                return false;
            }

            this.isOpen = false
            this.$emit('success', this.formData)
        }
    },
    data () {
        return {
            isOpen: false,
            formData: {
                account: '', contacts: '', phone: '', email: '',
                content: ''
            }
        }
    }
}
</script>

<style lang="less" scoped>
.product_consult {
    background: #ddebfb;
    input, textarea { padding: 5px 10px; box-sizing: border-box; background: #fff; border: 1px solid #444; }
    .hd {
        text-align: center; font-size: 26px; padding: 20px 0;
        img {
            vertical-align: middle;
        }
    }
    .form {
        padding: 0 50px;
        dl {
            overflow: hidden; margin-bottom: 10px;
            dd {
                float: left; width: 25%;
                padding-right: 5px; box-sizing: border-box;
                label { margin-right: 5px; }
                input { width: 100px; }
            }
        }
        .textarea {
            position: relative;
            textarea { width: 100%; height: 300px; margin-bottom: 10px; }
            span { position: absolute; left: 10px; bottom: 20px; color: #aaa; }
        }
    }
    .bm {
        padding: 0 50px 50px;
        a {
            font-size: 16px; color: #444; text-decoration: none;
            padding: 0 40px;
        }
    }
}
</style>