<template>
    <!-- title="我要咨询"  -->
    <el-dialog class="custom" width="650px" :modal="false" :visible.sync="isOpen"
        :before-close="closed" :close-on-click-modal="false" v-dialogDrag >
        
        {{ ProductDetail }}

        <div class="product_buy">
            <div class="hd">扫码付款</div>
            <div class="form">
                <dl>
                    <dd><label>用户名：</label><input type="text" ref="account" v-model="formData.account" placeholder="请输入用户名"></dd>
                    <dd><label>联系人：</label><input type="text" ref="contacts" v-model="formData.contacts" placeholder="请输入联系人"></dd>
                    <dd><label>电　话：</label><input type="text" ref="phone" v-model="formData.phone" placeholder="请输入电话"></dd>
                </dl>
                <dl>
                    <dd><label>软件名称：</label><input type="text" ref="account" v-model="formData.account" placeholder="请输入软件名称"></dd>
                    <dd><label>价格：</label><input type="text" ref="contacts" v-model="formData.contacts" placeholder="请输入价格"></dd>
                </dl>
                <dl>
                    <dd><label>定金：</label><input type="text" ref="account" v-model="formData.account" placeholder="请输入定金"></dd>
                    <dd><label>余款：</label><input type="text" ref="contacts" v-model="formData.contacts" placeholder="请输入余款"></dd>
                    <dd><label>全款：</label><input type="text" ref="contacts" v-model="formData.contacts" placeholder="请输入全款"></dd>
                </dl>
            </div>
            <div class="qrcode">
                <ul>
                    <li><label>微信</label><img src=""></li>
                    <li><label>支付宝</label><img src=""></li>
                </ul>
            </div>
            <div class="bm">
                <div class="rr text-right">
                    <a href="javascript:;">已付款</a>
                    <a href="javascript:;" @click="submit">提交</a>
                </div>
                <div class="clr"></div>
            </div>
        </div>

        <DialogAlert :open="$dialogAlert.isOpen" :message="$dialogAlert.message" @close="closeAlert" />

    </el-dialog>
</template>

<script>
import Cookie from 'vue-cookie'
import DialogAlert from '@src/components/dialog.alert/dialog.alert'

export default {
    components: { DialogAlert },
    props: {
        'title': { default: '未命名' },
        'open': { default: false },
        'data': { default: {} }
    },
    watch: {
        'open' (v) {
            this.isOpen = v
            if (v) {

                if ('id' in this.data) {
                    this.GetProductDetail(this.data.id)
                }

                const UserInfo = Cookie.get('UserInfo')
                if (UserInfo) {
                    const UserInfo_JSON = JSON.parse(UserInfo)
                    
                    // this.formData['account'] = UserInfo_JSON.account
                    // this.formData['contacts'] = UserInfo_JSON.contacts
                    // this.formData['phone'] = UserInfo_JSON.phone
                }
            }
        },
    },
    methods: {

        // [Get] 软件详情
		GetProductDetail (id) {
			this.axios.get(`/software/selectDetail?id=${id}`).then(rs => {
				if (rs.data.code == 0) {
                    this.ProductDetail = rs.data.data
                    
                    

				}
			})
		},

        closeAlert () {
            this.$refs[this.$dialogAlert.ref].focus()
            this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: false, message: '', ref: '' })
            this.$forceUpdate()
        },

        closed () {
            this.isOpen = false
            this.$emit('close', true)
        },
        submit () {

            // if (!this.formData.account) {
			// 	this.$dialogAlert = Object.assign(this.$dialogAlert, { isOpen: true,
            //         message: '请填写用户名', ref: 'account' })
            //     this.$forceUpdate();
            //     return false;
            // }

            this.isOpen = false
            this.$emit('success', this.formData)
        }
    },
    data () {
        return {
            isOpen: false,
            ProductDetail: {},
            formData: {
                userId: '', userName: '', softwareId: '', softwareName: '', softwarePrice: ''
            }
        }
    }
}
</script>

<style lang="less" scoped>
.product_buy {
    input, textarea { padding: 5px 10px; box-sizing: border-box; background: #fff; border: 1px solid #444; }
    .hd {
        text-align: center; font-size: 26px; padding: 20px 0;
    }
    .form {
        padding: 0 40px;
        dl {
            overflow: hidden; margin-bottom: 10px;
            dd {
                float: left; width: 33.33%;
                padding-right: 5px; box-sizing: border-box;
                label { display: inline-block; width: 70px; margin-right: 5px; }
                input { width: 100px; }
            }
        }
        .textarea {
            position: relative;
            textarea { width: 100%; height: 300px; margin-bottom: 10px; }
            span { position: absolute; left: 10px; bottom: 20px; color: #aaa; }
        }
    }
    .bm {
        padding: 0 50px 50px;
        a {
            font-size: 16px; color: #444; text-decoration: none;
            padding: 0 40px;
        }
    }
}
</style>