<template>
	<div class="products">
		<div class="wrap">
			<div class="wrap-1000">
				<div class="header">
					<img class="logo" src="./products_title.png" alt="软件产品" />
					<a href="javascript:;" @click="isDialogReadmeOpen = true" class="readme">使用软件须知</a>
				</div>
				<div class="content">
					<ul class="pro-list" v-if="ProductData && 'content' in ProductData">
						<li v-for="item in ProductData.content" :key="item.id">
							<div class="pro-item">
								<table>
									<tr><td colspan="2">名称：{{ item.name }}</td></tr>
									<tr><td>价格：{{ (item.price / 100).toFixed(2) }}</td><td class="profile">简介</td></tr>
								</table>
								<a href="javascript:;" class="pro-item-buy" @click="isDialogProductBuyOpen = true; dialogProductBuy_data = item; "><span><i>买</i></span></a>
							</div>
						</li>
					</ul>
					<div class="clr"></div>
				</div>
				<div class="bottom"><a href="javascript:;" @click="isDialogProductMoreOpen = true">更多软件</a></div>
				<div class="footer">
					<p>广东省汕头市澄海区白云软件开发有限公司</p>
					<p>粤ICP备16007973号</p>
				</div>
			</div>
		</div>

		<DialogReadme :open="isDialogReadmeOpen" @close="isDialogReadmeOpen = false"/>
        <DialogProductMore :open="isDialogProductMoreOpen" @close="isDialogProductMoreOpen = false"/>
		<DialogProductBuy :open="isDialogProductBuyOpen" @close="isDialogProductBuyOpen = false" :data="dialogProductBuy_data"/>

	</div>
</template>

<script>
import DialogReadme from '@src/components/dialog.readme/dialog.readme'
import DialogProductMore from '@src/components/dialog.product.more/dialog.product.more'
import DialogProductBuy from '@src/components/dialog.product.buy/dialog.product.buy'

export default {
	components: { DialogReadme, DialogProductMore, DialogProductBuy },
	methods: {
		// [Get] 软件列表
		GetProductData () {
			this.axios.get('/software/selectList').then(rs => {
				if (rs.data.code == 0) {
					this.ProductData = rs.data.data
				}
			})
		}
	},
	mounted () {
		this.GetProductData()
	},
	data () {
		return {
			isDialogProductBuyOpen: false, dialogProductBuy_data: {},
			isDialogReadmeOpen: false, isDialogProductMoreOpen: false,
			ProductData: {}
		}
	}
}
</script>
<style lang="less">
@import url('Products');
</style>
