<template>
    <!-- :title="`使用软件须知`" -->
    <el-dialog class="custom" width="940px" :modal="false" :visible.sync="isOpen" 
        :before-close="closed" :close-on-click-modal="false" v-dialogDrag >
        
        <div class="readme">
            <div class="title"><img src="./readme_title.png" alt="使用软件须知"></div>
            <div class="content">
                <p>软件管理员只能有一个,管理员密码要保管好，所有与授权相关的都需要管理操作才能完成，管理员有义务定期检查软件帐号的权限是否合理。软件使用请一人一个帐号，第一次使用软件请一定要修改自己的登陆密码，不要把帐号密码告诉其它的同事，以免其它人以你的名义违规操作软件，因为软件的操作记录是以帐号为依据的。请牢记自己的用户名和密码，建议使用数字、字母大小写组合的密码。为了能更加流畅的使用本系统，建议使用火狐浏览器或谷歌浏览器打开本软件，要安装火狐浏览器请点这里到官网下载。离开电脑前或下班了记得退出软件，以免其它人在你的电脑上用你的帐号修改资料。数据录入一定要及时、规范，不能物料都从仓库领走一个星期了，系统中还没记录。</p>
                <div class="next">
                    <a href="javascript:;" v-if="isRoute != undefined" v-route-to="`/products`">知道了</a>
                    <a href="javascript:;" v-else @click="closed">关闭</a>
                </div>
            </div>
           
        </div>

    </el-dialog>
</template>

<script>
export default {
    components: { },
    props: {
        'title': { default: '未命名' },
        'open': { default: false },
        'isRoute': {}
    },
    watch: {
        'open' (v) {
            this.isOpen = v
            console.log(`isRoute: ${this.isRoute}`)
        },
    },
    methods: {
        closed () {
            this.isOpen = false
            this.$emit('close', true)
        }
    },
    data () {
        return {
            isOpen: false
        }
    }
}
</script>
<style lang="less" scoped>
.readme {
    background: #f6fff0 url('./readme_flower.png') no-repeat 0 100%;
    .title {
        width: 100%; text-align: center; padding: 30px 0;
        img { margin-left: -100px; }
    }
    .content {
        font-size: 18px; color: #333;
        padding: 30px; box-sizing: border-box;
        min-height: 500px; overflow: hidden;
        p {
            text-align: justify;
        }
        .next {
            float: right; margin-top: 100px; margin-right: 100px;
            font-size: 28px;
            a {
                color: #0f54b1; text-decoration: none;
                &:active { color: #333; }
            }
        }
    }
}
</style>