<!-- 
    main.js 全局变量
    Vue.prototype.$dialogAlert = { isOpen: false, message: '' }
    ..
    <DialogAlert :open="$dialogAlert.isOpen" :message="$dialogAlert.message" @close="$dialogAlert = Object.assign($dialogAlert, { isOpen: false, message: '' }); $forceUpdate();" />
    ..
    import DialogAlert from '@src/components/dialog.alert/dialog.alert'
    ..
    components: { DialogAlert },
    ..
-->
<template>
    <el-dialog class="custom" width="260px" :modal="false" :visible.sync="isOpen"
        :before-close="closed" :close-on-click-modal="false" :show-close="false" v-dialogDrag>

        <div class="custom-alert">
            <div class="btn-close" @click="closed"></div>
            <div class="content" v-html="message"></div>
        </div>

    </el-dialog>
</template>

<script>
export default {
    props: {
        'open': { default: false },
        'message': { default: '-' }
    },
    watch: {
        'open' (v) {
            this.isOpen = v
        },
    },
    methods: {
        closed () {
            this.isOpen = false
            this.$emit('close', true)
        }
    },
    data () {
        return {
            isOpen: false
        }
    }
}
</script>

<style lang="less" scoped>
.custom-alert {
    width: 260px; height: 180px;
    background: url('../../assets/alert_bg.png') no-repeat;
    .content {
        text-align: center; font-size: 24px;
        padding: 75px 30px 0; box-sizing: border-box;
    }
}
.btn-close {
    position: absolute; top: 15px; right: 15px; z-index: 101;
    width: 30px; height: 30px; cursor: pointer;
    background: url('../../assets/alert_close.png') no-repeat;
}
</style>